import React, { useState, useEffect, useCallback } from 'react';
import R0 from './R0.svg';
import LOGO from './logo.svg';
import THANKS from './thanks.svg';
import './App.css';
import Charts from './Charts';
import { Radio, Row, Col, } from 'antd';
import { FundViewOutlined, SearchOutlined, LayoutOutlined, MessageOutlined, GithubOutlined } from '@ant-design/icons';


function App() {
  const [streamline, setStreamline] = useState('twentyFive');
  const [windowPageYOffset, setWindowPageYOffset] = useState(0);
  const scroll = useCallback(() => {
    setWindowPageYOffset(window.pageYOffset);
  }, [window]);
  function onChange(e) {
    setStreamline(e.target.value);
  }
  useEffect(() => {
    window.addEventListener('scroll', scroll);
    return () => window.removeEventListener('scroll', scroll);
  }, [scroll]);
  return (
    <div className="App">
      <header className={windowPageYOffset > 48 ? 'min' : 'max'} >
        <div className="header-layout">
          <div className="App-title">
            <img src={LOGO} alt=""/>
          </div>
          <a href="https://github.com/aute/Visualization-infectious-diseases"> <GithubOutlined style={{fontSize:'2rem',color:'#fff'}}/></a>
        </div>
      </header>
      <main>
        <h1 className="App-chart-title"></h1>
        <Radio.Group onChange={onChange} defaultValue="twentyFive">
          <Radio.Button value="twentyFive">25 </Radio.Button>
          <Radio.Button value="all"></Radio.Button>
        </Radio.Group>
        <div className="App-chart">
          <Charts streamline={streamline} />
        </div>
        <article className="App-article">
          <Row gutter={[16, 16]}>
            <Col className="gutter-row" md={12} sm={24}>
              <div className="App-R0-instructions">
                <img src={R0} className="R0" alt="R0" />
                <div style={{ backgroundColor: '#000' }}><article className="App-R0-instructions-article">
                  <p>
                    <strong> R0 </strong> R0  Basic reproduction number 
                  </p>
                  <p>
                    <strong>: </strong>
                  </p>
                  <p>
                    <strong> R0 {`<`} 1</strong>
                    </p>
                  <p>
                    <strong> R0 {`>`} 1</strong>epidemic
                    </p>
                  <p>
                    <strong> R0 = 1</strong>
                  </p>
                </article></div>


              </div>
            </Col>
            <Col className="gutter-row" md={12} sm={24}>
              <div className="App-chart-instructions">
                <h2></h2>
                <ul>
                  <li>
                    <h4><FundViewOutlined /> </h4>
                    
              </li>
                  <li>
                    <h4><SearchOutlined /> </h4>
                     25 
              </li>
                  <li>
                    <h4><LayoutOutlined /> </h4>
                     20%R0  20 
              </li>
                  <li>
                    <h4><MessageOutlined /> </h4>
                      
              </li>
                </ul>
              </div>
            </Col>
          </ Row>
        </article>
      </main>
      <footer>
        <h5>BY AUTE</h5>
        <img src={THANKS} alt="* MarlborAngie2000  "/>
      </footer>
    </div>
  );
}

export default App;
