import * as R from 'ramda';
export const zh_map_util = (MAP, RAW_DATA, EMPTY_STRING) => {
    const zh = MAP[RAW_DATA];
    return zh
        ? zh
        : EMPTY_STRING
            ? ''
            : RAW_DATA;
};

